import { IconProp, SizeProp } from "@fortawesome/fontawesome-svg-core";
import { solid, brands } from "@fortawesome/fontawesome-svg-core/import.macro";

interface IIcon {
  url: string;
  icon: IconProp;
  color?: string;
  size?: SizeProp;
  className?: string;
}

export default class IconModel implements IIcon {
  url: string;
  icon: IconProp;
  color?: string | undefined;
  size?: SizeProp | undefined;
  className?: string | undefined;

  constructor(
    url: string,
    icon: IconProp,
    color?: string,
    size?: SizeProp,
    className?: string
  ) {
    this.url = url;
    this.icon = icon;
    this.size = size;
    this.color = color;
    this.className = className;
  }
}

export const githubIcon = new IconModel(
  "https://github.com/jitendrapurohit12",
  brands("github"),
  "black",
  "2x",
  "icon"
);
