import React from "react";
import ProjectModel from "../../../classes/models/ProjectModel";
import AppButton from "./AppButton";

export default function ProjectUnitUI(props: { model: ProjectModel }) {
  var model = props.model;
  return (
    <div className="project_ui">
      <ImageUI model={model} />
      <Details model={model} />
    </div>
  );
}

function ImageUI(props: { model: ProjectModel }) {
  return (
    <img
      className="project_image"
      src={props.model.image}
      alt={props.model.name}
    />
  );
}

function Details(props: { model: ProjectModel }) {
  var model = props.model;

  function openProject() {
    window.open(model.projectURL, "_blank");
  }

  return (
    <div className="project_details">
      <TitleUI model={model} />
      <body className="project_description">{model.description}</body>
      <HeadingUI heading="TECHSTACK" />
      <div className="techstacks">
        {props.model.techstack.map((stack) => (
          <body className="techstack">{stack}</body>
        ))}
      </div>
      {model.review && <HeadingUI heading="REVIEW" />}
      {model.review && <ReviewUI review={model.review} />}
      {model.projectURL && (
        <AppButton title="View Project" callback={() => openProject()} />
      )}
    </div>
  );
}

function TitleUI(props: { model: ProjectModel }) {
  return (
    <div className="project_title">
      <img
        className="project_icon"
        src={props.model.icon}
        alt={props.model.image}
      />
      <h1 className="project_name">{props.model.name.toUpperCase()}</h1>
    </div>
  );
}

function HeadingUI(props: { heading: string }) {
  return <h1 className="subheading">{props.heading}</h1>;
}

function ReviewUI(props: { review?: string }) {
  return <body className="review">"{props.review}"</body>;
}
